import { assign, trim, debounce, size } from 'lodash'
import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import LocationRepository from '@/services/repositories/location'
import getRepository from '@/services'
// import { LocationEntiry } from '@/services/entities/location'
