const beAudit = namespace('beAudit')

@Component({ components: {} })
export default class FilterPl9i extends Mixins(PopupMixin) {
  // @Prop({ required: true, type: Object }) filters!: Record<string, any>
  @Prop({ required: false, type: String, default: 'Tìm kiếm...' }) searchPlaceholder!: string
  @beAudit.Action('setReportFilter') setReportFilter!: (data: { contract: string; filters: Record<string, any>; data: Record<string, any> }) => void
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any>; data: Record<string, any> }

  locationRes: LocationRepository | undefined = undefined

  isShow = false
  search = ''
  groupFilters: Record<string, any> = {
    kpiMonth: new Date().getMonth() + 1,
    kpiYear: new Date().getFullYear()
  }
  filters: Record<string, any> = {
    kpiMonth: new Date().getMonth() + 1,
    kpiYear: new Date(),
    kpiCode: 'REVENUE_YEAR1'
  }

  isLoading = false

  chitieu = [
    {
      id: 0,
      label: 'Chỉ tiêu doanh số FYP',
      value: 'REVENUE_YEAR1'
    },
    {
      id: 1,
      label: 'Chỉ tiêu phí',
      value: 'COMMISSION'
    }
  ]

  query = {
    areas: {
      i: 0,
      ps: 20,
      search: ''
    },
    branches: {
      i: 0,
      ps: 20,
      search: ''
    },
    offices: {
      i: 0,
      ps: 20,
      search: ''
    }
  }

  disable = {
    area: false,
    branch: false,
    office: false,
    member: false
  }
  sortType = '' // BRANCH_ID_INCREASE, REVENUE_INCREASE, COMMISSION_INCREASE, BRANCH_ID_DECREASE, REVENUE_DECREASE, COMMISSION_DECREASE

  get sorts(): Array<Record<string, any>> {
    return [
      {
        command: 1,
        command_pri: 1,
        label: 'Đơn vị kinh doanh',
        divided: false
      },
      {
        command: 8,
        command_pri: 9,
        label: 'Chỉ tiêu',
        divided: true
      },
      {
        command: 2,
        command_pri: 3,
        label: 'Kết quả',
        divided: false
      },
      {
        command: 5,
        command_pri: 7,
        label: 'Tỷ lệ hoàn thành',
        divided: false
      }
    ]
  }
  filter1 = 0
  dataChange: any = {}
  dataSelect: Record<string, any> = {
    areas: [],
    branches: [],
    offices: []
  }
  checkChangOrRemove(type: string, code: string, item: Record<string, any>, id: number): void {
    this.dataChange[code] = this.dataChange[code] && this.dataChange[code].length > 0 ? this.dataChange[code] : []
    if (type == 'change') {
      let hasItem = this.dataChange[code] && this.dataChange[code].length > 0 && item ? this.dataChange[code].some((el: Record<string, any>) => el.id == item.id) : false
      if (!hasItem && item) {
        this.dataChange[code].push(item)
      }
    } else {
      if (this.dataChange[code] && this.dataChange[code].length > 0) {
        let newList = this.dataChange[code].filter((el: Record<string, any>) => el.id != id)
        this.dataChange[code] = newList
      } else {
        this.dataChange[code] = []
      }
    }
  }
  checkCode(type: string, code: string | any, id: number): void {
    let item: Record<string, any> = {}
    switch (code) {
      case 'offices':
        item = this.dataSelect.offices.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
      case 'branches':
        item = this.dataSelect.branches.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
      case 'areas':
        item = this.dataSelect.areas.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
    }
  }
  handleChange(type: string, code: string | any, id: Array<number>): void {
    this.$forceUpdate()
    const lastId = id[id.length - 1]
    const stateFilter = this.getFilter(this.$route.name as string)
    if (stateFilter?.data) {
      this.dataChange = stateFilter?.data
    } else {
      this.dataChange = {}
    }
    if (type === 'clear') {
      this.dataChange[code] = []
    } else {
      this.checkCode(type, code, lastId)
    }
  }
  @Watch('search', { immediate: false }) watchSearch(new_: string): void {
    console.log('watch search')

    this.searchText(trim(new_))
  }

  searchText(value: string): void {
    this.$emit(
      'change',
      assign(this.filters, {
        keywordString: value
      })
    )
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: assign(this.filters, {
        keywordString: this.search
      }),
      data: this.getFilter(this.$route.name as string).data || {}
    })
    //   this.setReportFilter({
    //     report: this.type,
    //     filters: assign(this.groupFilters, {
    //       search: value,
    //       sortType: this.sortType
    //     })
    //   })
  }

  handleClick(): void {
    this.isShow = true
    //   this.setOpenPopup({
    //     popupName: 'popup-filter-9i',
    //     isOpen: true
    //   })
  }

  handeChangeFilters(filters: Record<string, any>): void {
    this.groupFilters = {
      ...this.groupFilters,
      ...filters
    }
    console.log('when submit groupFilters', this.groupFilters)
    console.log('when submit filters', filters)
    //   this.$emit(
    //     'change',
    //     assign(this.groupFilters, {
    //       search: this.search,
    //       sortType: this.sortType
    //     })
    //   )
    //   this.setReportFilter({
    //     report: this.type,
    //     filters: assign(this.groupFilters, {
    //       search: this.search,
    //       sortType: this.sortType
    //     })
    //   })
  }

  handleCommand(value: Record<string, any>): void {
    if (this.sortType === value.command) {
      this.sortType = ''
      this.filters.orderByClause = ''
    } else {
      this.sortType = value.command
      this.setOrderByClause(value)
    }
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: {
        ...this.filters
      },
      data: this.getFilter(this.$route.name as string) || {}
    })
    this.$emit('change', this.filters)
  }

  setOrderByClause(value: Record<string, any>): void {
    if (this.filters.kpiCode === 'REVENUE_YEAR1') {
      this.filters.orderByClause = value.command
    } else {
      this.filters.orderByClause = value.command_pri
    }
  }

  created(): void {
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })
  }

  async searchArea(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.areas = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('area', {
        search: trim(search),
        i: this.query.areas.i,
        ps: this.query.areas.ps
      })
      if (isAdd) {
        const a = result || []
        this.dataSelect.areas = [...this.dataSelect.areas, ...a]
      } else {
        this.dataSelect.areas = result || []
      }

      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataSelect.areas = []
    }
  }

  async searchBranch(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })

      if (isAdd) {
        const a = result || []
        this.dataSelect.branches = [...this.dataSelect.branches, ...a]
      } else {
        this.dataSelect.branches = result || []
      }

      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataSelect.branches = []
    }
  }

  async searchOffice(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.offices = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps
      })

      if (isAdd) {
        const a = result || []
        this.dataSelect.offices = [...this.dataSelect.offices, ...a]
      } else {
        this.dataSelect.offices = result || []
      }

      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataSelect.offices = []
    }
  }
  async handleShowPopper(): Promise<any> {
    this.$forceUpdate()

    const filters = this.getFilter(this.$route?.name as string)
    await Promise.all([this.searchArea(''), this.searchBranch(''), this.searchOffice('')]).then(() => {
      if (filters) {
        this.groupFilters = assign(this.groupFilters, filters.filters)
        if (Object.prototype.hasOwnProperty.call(filters, 'data')) {
          this.dataChange = filters.data
          for (let el in this.dataChange) {
            let list = [...this.dataSelect[el], ...this.dataChange[el]]
            this.dataSelect[el] = [...new Map(list.map((item: Record<string, any>) => [item['id'], item])).values()]
          }
        }
      }
    })
  }
  resetData(): void {
    //@ts-ignore
    this.$refs.filterForm.clearValidate()
    this.filters.resultPercent = ''
    this.filters = {
      kpiMonth: new Date().getMonth() + 1,
      kpiYear: new Date(),
      kpiCode: 'REVENUE_YEAR1'
    }
  }

  remoteMethodGetArea(query: string): void {
    this.isLoading = true
    this.query.areas.search = query
    this.query.areas.i = 0
    this.query.areas.ps = 20
    const a = debounce(this.searchArea, 500)
    a(query)
  }

  remoteMethodGetBranch(query: string): void {
    this.isLoading = true
    this.query.branches.search = query
    this.query.branches.i = 0
    this.query.branches.ps = 20
    const a = debounce(this.searchBranch, 500)
    a(query)
  }

  remoteMethodGetOffice(query: string): void {
    this.isLoading = true
    this.query.offices.search = query
    this.query.offices.i = 0
    this.query.offices.ps = 20
    const a = debounce(this.searchOffice, 500)
    a(query)
  }
  loadAreas(): void {
    console.log(1)
    if (size(this.dataSelect.areas) === this.query.areas.i + this.query.areas.ps) {
      this.query.areas.i = size(this.dataSelect.areas)
      const a = debounce(this.searchArea, 500)
      a(this.query.areas.search, true)
    }
  }

  loadBranches(): void {
    console.log(1)
    if (size(this.dataSelect.branches) === this.query.branches.i + this.query.branches.ps) {
      this.query.branches.i = size(this.dataSelect.branches)
      const a = debounce(this.searchBranch, 500)
      a(this.query.branches.search, true)
    }
  }
  loadOffices(): void {
    console.log(1)
    if (size(this.dataSelect.offices) === this.query.offices.i + this.query.offices.ps) {
      this.query.offices.i = size(this.dataSelect.offices)
      const a = debounce(this.searchOffice, 500)
      a(this.query.offices.search, true)
    }
  }
  handleSubmit(type: 'reset' | 'submit'): void {
    if (type === 'reset') {
      this.resetData()
      return
    }
    this.isShow = false
    this.setReportFilter({
      contract: this.$route?.name as string,
      filters: {
        ...this.filters
      },
      data: this.dataChange
    })
    this.$emit('change', this.filters)
  }
  handleChangeKpiCode(): void {
    this.filters.orderByClause = ''
    this.sortType = ''
    this.$emit('change', this.filters)
  }
}
